.footer {
    width: 100%;
    position: relative;
    padding-bottom: 20px;
    margin-top: 20px;
}

.footer__body {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding-top: 10px;
    color: grey;
}

.footer__body::after {
    width: 100%;
    content: "";
    height: 2px;
    background-color: grey;
    border-radius: 5px;
    position: absolute;
    top: 0px;
    left: 0;
}

.footer__link {
    cursor: pointer;
}